import {
    SET_ANNOUNCEMENTS,
    LOADING_DATA,
    LIKE_ANNOUNCEMENT,
    UNLIKE_ANNOUNCEMENT,
    DELETE_ANNOUNCEMENT,
    CLEAR_ERRORS,
    SET_ERRORS,
    LOADING_UI,
    POST_ANNOUNCEMENT,
    SET_ANNOUNCEMENT,
    STOP_LOADING_UI,
    SUBMIT_COMMENT,
    LOADING_USER,
    APPEND_IMAGEURL_TO_ANNOUNCEMENT, SET_CALENDAR_EVENTS
} from "../types";
import axios from 'axios';
import {transpose} from "../../util/array_utils";
import {csvToArray} from "../../util/csv_utils";

const CAL_SRC_POINTER_URL = "https://docs.google.com/spreadsheets/d/e/2PACX-1vTXWnYUXX_mMxaN-ejalcrLUcKowe8L1geqjOT5WOLxcjwLyAzvNsgnwixPc0u3ycQfYPr9iRH6mJoB/pub?gid=0&single=true&output=csv";

export const getAnnouncements = () => dispatch => {
    dispatch({ type: LOADING_DATA });
    axios.get("/announcements")
        .then(res => {
            dispatch({
                type: SET_ANNOUNCEMENTS,
                payload: res.data.map(s => ({...s, announcementId: s.announcementId}))
            })
        })
        .catch(err => {
            dispatch({
                type: SET_ANNOUNCEMENTS,
                payload: []
            })

        })
};

export const getAnnouncement = (announcementId) => dispatch => {
    dispatch({ type: LOADING_UI });
    axios.get(`/announcement/${announcementId}`)
        .then(res => {
            dispatch({
                type: SET_ANNOUNCEMENT,
                payload: {...res.data, announcementId: res.data.announcementId}
            });
            dispatch({
                type: STOP_LOADING_UI
            })
        })
        .catch(err => {
            console.log(err);
        })
};

export const postAnnouncement = (newAnnouncement) => dispatch => {
    dispatch({ type: LOADING_UI });
    axios.post("/announcement", newAnnouncement)
        .then(res => {
            dispatch({
                type: POST_ANNOUNCEMENT,
                payload: {...res.data, announcementId: res.data.announcementId}
            });
            dispatch(clearErrors())
        })
        .catch(err => {
            dispatch({
                type: SET_ERRORS,
                payload: err.response.data
            })
        })
};

export const updateAnnouncement = (announcementId, body) => dispatch => {
    dispatch({ type: LOADING_UI });
    axios.post(`/announcement/${announcementId}`, { body: body })
        .then(() => {
            dispatch(getAnnouncements());
        })
        .catch(err => {
            dispatch({
                type: SET_ERRORS,
                payload: err.response.data
            })
        })
};

