export const getUserData = (userHandle) => dispatch => {
    dispatch({ type: LOADING_DATA });
    axios.get(`/user/${userHandle}`)
        .then((res) => {
            dispatch({
                type: SET_ANNOUNCEMENTS,
                payload: res.data.announcements
            })
        })
        .catch(() => {
            dispatch({
                type: SET_ANNOUNCEMENTS,
                payload: null
            });
        });
};

export const getCalendarData = (calSrc) => dispatch => {
    const DATE_RE = /(\d{2})\/(\d{2})\/(\d{4})/g;
    fetch(CAL_SRC_POINTER_URL)
        .then(rsp => {
            return rsp.text();
        })
        .then(rsp => {
            const line = rsp.data.split("\n")[0];
            const url = line.split(",")[1].trim()
            return fetch(url)
        })
        .then(rsp => {
            return rsp.text();
        })
        .then(rsp => {
            const text = rsp.data;
            const eventsRaw = transpose(csvToArray(text)).flat()
            const events = [];
            eventsRaw.forEach((d, i) => {
                if (d.match(DATE_RE)) {
                    const m = DATE_RE.exec(d)
                    const eventMo = parseInt(m[1]);
                    const eventDa = parseInt(m[2]);
                    const eventYr = parseInt(m[3]);
                    let eventTitle = eventsRaw[i + 1];
                    if (!!eventTitle) {
                        let start = new Date(eventYr, eventMo - 1, eventDa);
                        let end = new Date(eventYr, eventMo - 1, eventDa);
                        if (eventTitle.includes("预查带领")) {
                            eventTitle = "预查\n" + eventTitle
                            start = new Date(eventYr, eventMo - 1, eventDa, 11, 15, 0);
                            end = new Date(eventYr, eventMo - 1, eventDa, 12, 15, 0);
                        } else if (eventTitle.includes("诗歌带领")) {
                            eventTitle = "团契查经\n" + eventTitle
                            start = new Date(eventYr, eventMo - 1, eventDa, 19, 20, 0);
                            end = new Date(eventYr, eventMo - 1, eventDa, 21, 30, 0);
                        }
                        const event = {
                            id: i,
                            title: eventTitle,
                            start: start,
                            end: end,
                        }
                        events.push(event);
                    }
                }
            })
            dispatch({
                type: SET_CALENDAR_EVENTS,
                payload: events
            })
        })
        .catch(err => {
            console.log(err);
        })
}

export const clearErrors = () => dispatch => {
    dispatch({ type: CLEAR_ERRORS })
};

export const uploadImageForPost = (announcementId, formData) => (dispatch) => {
    dispatch({ type: LOADING_USER });
    axios.post(`/announcement/${announcementId}/image`, formData)
        .then(res => {
            dispatch({
                type: APPEND_IMAGEURL_TO_ANNOUNCEMENT,
                payload: res.data.imageUrl
            })
        })
        .catch(err => console.log(err))
};
