import React, { Component } from 'react'
import withStyles from '@material-ui/core/styles/withStyles';
import PropTypes from 'prop-types';
// Redux
import { connect } from "react-redux";

import { Calendar as RBCalendar, Views, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import {getCalendarData} from "../../redux/actions/dataActions";

const now = new Date()

const localizer = momentLocalizer(moment)
const styles = () => ({
    container: {
    },
    eventContent: {
        'white-space': 'pre-wrap'
    },
    eventWrapper: {
    },
    eventWrapperNoLabel: {
        '& .rbc-event-label': {
            display: "none"
        }
    }
});

class Calendar extends Component {
    state = {
        view: Views.WEEK,
        events: []
    };

    constructor(props) {
        super(props);
        this.view = this.getView(this.props.view)
    }

    componentDidMount() {
        this.props.getCalendarData()
    }

    getView(view) {
        if (view === "month") {
            return Views.MONTH;
        }
        if (view === "week") {
            return Views.WEEK;
        }
        if (view === "day") {
            return Views.DAY;
        }
        if (view === "agenda") {
            return Views.AGENDA
        }
        return Views.WEEK;
    }

    render() {
        const { classes, calendarEvents } = this.props;
        let allViews = [Views.MONTH, Views.WEEK, Views.DAY, Views.AGENDA]

        const titleAccessor = (event) => {
            if (this.state.view === Views.MONTH) {
                const titleLines = event.title.split("\n")
                return (
                    <div className={classes.eventContent}>
                        {titleLines[0]}
                    </div>
                );
            }
            return (
                <div className={classes.eventContent}>
                    {event.title}
                </div>
            );
        };

        const eventWrapper = eventWrapperProps => {
            const eventTimeMin = Math.floor( (eventWrapperProps.event.end - eventWrapperProps.event.start) / (1000*60))
            return (
                <div className={eventTimeMin < 60 ? classes.eventWrapperNoLabel : classes.eventWrapper}>
                    {eventWrapperProps.children}
                </div>
            )
        };

        return <>
            <div className={classes.container}>
                <RBCalendar
                    events={calendarEvents ? calendarEvents : []}
                    view={this.state.view}
                    views={allViews}
                    step={60}
                    defaultDate={now}
                    components={{
                        eventWrapper: eventWrapper
                    }}
                    localizer={localizer}
                    style={{ height: 1000 }}
                    titleAccessor={titleAccessor}
                    onView={(view) => {
                        this.setState({view: view})
                    }}
                    min={moment('6:00am', 'h:mma').toDate()}
                />
            </div>
        </>
    }
}

Calendar.propTypes = {
    classes: PropTypes.object.isRequired,
    openDialog: PropTypes.bool,
    view: PropTypes.string,
    getCalendarData: PropTypes.func.isRequired,
    calendarEvents: PropTypes.array
};

const mapStateToProps = state => ({
    calendarEvents: state.data.calendarEvents
});

export default connect(mapStateToProps, {getCalendarData})(withStyles(styles)(Calendar));